
body  {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}
html {
    height: 100%;

}

.header {
    padding: 2em;
    justify-content: center;
    display: flex;
    color: var(--black-coffee);
    /*background-color: var(--mint-cream);*/
    h1 {
        text-align: center;
    }
}

.content {
    /*padding: 6em;*/
    max-width: 800px;
    margin: auto;
    width: -webkit-fill-available;
}

.footer {
    margin-top: 4em;
    padding: 2em;
    justify-content: center;
    display: flex;
    /*color: var(--cadet-blue);*/
    color: var(--black-coffee);
    font-weight: lighter;
    background-color: var(--mint-cream);

}
